
.small-paddings {
  padding: 10px 5%;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
  z-index: 1;
}
.btn-large {
  line-height: 20px;
}
.container .row {
  margin: 10px 0;
}
.cart-wrapper {
  position: absolute;
  top: 60px;
  right: 0;
  width: 450px;
  height: auto;
  background-color: #111;
  z-index: 99999;
}
.cart {
  position: absolute;
  top: 0;
  right: 1px;
  width: 450px;
  min-height: 200px;
  background-color: white;
  padding: 10%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.cart img {
    max-width: 100%;
}
.cart .row {
    margin: 10px auto;
}
.emptyOrder {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.emptyOrder .ready {
    margin-top: 10px;
}
.cart-items .row {
  margin: 10px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.cart-items .row .col:first-child {
    padding: 0 1rem 0 0;
}
.cart-items .row .col:last-child {
    padding: 0 0 0 1rem;
}
.cart-items .row .item-description {
    font-size: 0.9rem;
}
.plus, .minus {
  font-size: 1.5rem;
  cursor: pointer;
}
.quantity {
  margin: 0 8px;
  padding: 3px;
  border: 1px solid grey;
}
.disactivate {
  position: absolute;
  top: -60px;
  right: 60px;
  width: 50px;
  height: 60px;
  opacity: 0;
}
.icon {
  position: absolute;
  top: -60px;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #ba321a;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.reveal {
  position: absolute;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-animation: reveal 1s forwards;
          animation: reveal 1s forwards;
}
.btn, .btn-large {
  background: #1e5799;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #ba321a 0%, #e25609 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #ba321a 0%, #e25609 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$main, endColorstr=$checkout-active-light,GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.main {
  width: calc(100vw - 180px);
  height: 100vh;
  background: #eee;
  position: relative;
  display: -ms-grid;
  display: grid;
}
.choice {
  height: calc(100vh - 130px);
  margin-top: 0;
  overflow-y: scroll;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: initial;
      -ms-flex-align: initial;
          align-items: initial;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.choice .row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.fade-enter-active {
  -webkit-transition: all 0.7s ease;
  transition: all 0.7s ease;
}
.fade-enter, .fade-leave-active, .fade-leave-to {
  opacity: 0;
}
.slide-fade-enter-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.slide-fade-enter, .slide-fade-leave-to {
  -webkit-transform: translateX(100px);
          transform: translateX(100px);
  opacity: 0;
}
@-webkit-keyframes spin {
100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
}
}
@keyframes spin {
100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
}
}



