
.small-paddings {
  padding: 10px 5%;
}
.meal-popup {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}
.meal-popup_box {
    width: 100%;
    max-width: 768px;
    position: relative;
    z-index: 5;
    background: #eee;
    padding-bottom: 50px;
    margin: 100px auto;
}
.meal-popup_close {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background: transparent;
    width: 40px;
    height: 40px;
    -webkit-transition: background 0.5s ease;
    transition: background 0.5s ease;
    cursor: pointer;
}
.meal-popup_close:before, .meal-popup_close:after {
      content: "";
      display: block;
      position: absolute;
      top: 19px;
      left: 10px;
      width: 20px;
      height: 2px;
      background: #111;
}
.meal-popup_close:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.meal-popup_close:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
}
.meal-popup_close:hover {
      -webkit-animation: 0.5s rotate;
              animation: 0.5s rotate;
}
@-webkit-keyframes rotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
}
@keyframes rotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
}



